
'use client';

import LoadingLink from '@/components/loading-link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Eye, UserPlus, AlertCircle, Loader2, LogOut as LogOutIcon, LayoutDashboard } from 'lucide-react';
import { useRouter, useSearchParams } from 'next/navigation';
import React, { useEffect, useState, useActionState } from 'react';
import Script from 'next/script';
import { authenticateUserAction, getSessionUser, type AuthFormState } from '@/lib/actions';
import { useLoading } from '@/contexts/loading-context';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

declare global {
  interface Window {
    initGeetest4: any;
  }
}

export default function LoginPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [isDebugMode, setIsDebugMode] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState<boolean | null>(null);
  const [sessionUsername, setSessionUsername] = useState<string>('');
  const { startLoading, stopLoading } = useLoading();
  const [authMode, setAuthMode] = useState<'login' | 'register'>('login');

  const [sdkReady, setSdkReady] = useState(false);
  const [captchaObj, setCaptchaObj] = useState<any>(null);
  const [captchaResult, setCaptchaResult] = useState<any>(null);
  const [isCaptchaVerified, setIsCaptchaVerified] = useState(false);

  const initialState: AuthFormState = { message: null, errors: {}, success: false };
  const [state, formAction, isPending] = useActionState(
    async (prevState: AuthFormState, formData: FormData) => {
      startLoading();
      formData.append('authMode', authMode);
      if (captchaResult) {
        formData.append('lot_number', captchaResult.lot_number);
        formData.append('captcha_output', captchaResult.captcha_output);
        formData.append('pass_token', captchaResult.pass_token);
        formData.append('gen_time', captchaResult.gen_time);
      }

      let result;
      try {
        result = await authenticateUserAction(prevState, formData);
        if (result && (result.errors || (result.message && !result.success))) {
          stopLoading();
          if (result.errors?.captcha) {
            setIsCaptchaVerified(false);
            setCaptchaResult(null);
            captchaObj?.reset();
          }
        }
        return result;
      } catch (error: any) {
        if (error && typeof error === 'object' && 'digest' in error && typeof error.digest === 'string' && error.digest.startsWith('NEXT_REDIRECT')) {
          throw error;
        }
        console.error("Login/Register action failed on client:", error);
        stopLoading();
        return { message: '认证时发生意外错误。', errors: {form: ['认证时发生意外错误。']}, success: false };
      }
    },
    initialState
  );

  useEffect(() => {
    setIsDebugMode(process.env.NEXT_PUBLIC_DEBUG_MODE === 'true');
    async function checkLoginStatus() {
      try {
        const user = await getSessionUser();
        if (user) {
          setIsLoggedIn(true);
          setSessionUsername(user.username);
        } else {
          setIsLoggedIn(false);
        }
      } catch (e) {
         console.error("登录页面检查登录状态时出错:", e);
         setIsLoggedIn(false);
      }
    }
    checkLoginStatus();
  }, []);

  useEffect(() => {
    const geetestCaptchaId = process.env.NEXT_PUBLIC_GEETEST_CAPTCHA_ID;
    // console.log("NEXT_PUBLIC_GEETEST_CAPTCHA_ID on client:", geetestCaptchaId); // For debugging

    if (sdkReady && typeof window.initGeetest4 === 'function' && geetestCaptchaId) {
      // Destroy existing captcha instance if any
      if (captchaObj) {
        captchaObj.destroy();
        setCaptchaObj(null);
      }

      window.initGeetest4({
        captchaId: geetestCaptchaId,
        product: 'bind',
        language: 'zho-cn',
        onSuccess: (result: any) => {
          setCaptchaResult(result);
          setIsCaptchaVerified(true);
        },
        onError: (err: any) => {
          console.error("Geetest error:", err);
          setIsCaptchaVerified(false);
          setCaptchaResult(null);
        }
      }, (newCaptchaObj: any) => {
        setCaptchaObj(newCaptchaObj);
        if (newCaptchaObj) {
          const captchaContainer = document.getElementById('captcha-container');
          if (captchaContainer) {
            while (captchaContainer.firstChild) {
                captchaContainer.removeChild(captchaContainer.firstChild);
            }
            try {
                newCaptchaObj.appendTo('#captcha-container');
            } catch (appendError) {
                console.error("Geetest appendTo error:", appendError);
            }
          } else {
            console.warn("Geetest: captcha-container not found for appendTo.");
          }
        }
      });
    }
    
    // Cleanup when authMode changes or component unmounts
    return () => {
      if (captchaObj) {
        captchaObj.destroy();
        setCaptchaObj(null);
      }
    };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sdkReady, authMode]); // Re-init Geetest when authMode changes or SDK is ready

  const handleAuthModeChange = (newMode: 'login' | 'register') => {
    setAuthMode(newMode);
    // Reset captcha and form state when mode changes
    setIsCaptchaVerified(false);
    setCaptchaResult(null);
    // The useEffect for Geetest will handle captchaObj.reset() or destroy/recreate
    // Reset form action state
    // dispatch(initialState); // This is not how useActionState's dispatch works directly
    // To reset form errors, they are typically cleared when inputs change or form re-submits
  };


  const handleDebugLogin = () => {
    startLoading();
    router.push(`/dashboard?debug_user_id=dream_pep_id`);
  };

  const handleLogout = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    startLoading();
    event.currentTarget.submit();
  };


  if (isLoggedIn === null) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
      </div>
    );
  }

  const LoginPageContentWrapper = ({ children }: { children: React.ReactNode }) => (
    <div className="flex flex-col items-center justify-center flex-grow w-full bg-gradient-to-br from-background to-secondary/20 py-6">
      {children}
    </div>
  );


  if (isLoggedIn) {
    return (
      <LoginPageContentWrapper>
        <Card className="w-full max-w-md shadow-xl text-center">
          <CardHeader>
            <CardTitle className="text-2xl font-bold">您已登录</CardTitle>
            <CardDescription>您当前以 {sessionUsername} 的身份登录。</CardDescription>
          </CardHeader>
          <CardContent>
            <p>您想退出登录吗？或者前往仪表盘？</p>
          </CardContent>
          <CardFooter className="flex flex-col gap-3">
            <form action="/api/auth/logout" method="POST" onSubmit={handleLogout} className="w-full">
              <Button type="submit" variant="destructive" className="w-full">
                <LogOutIcon className="mr-2 h-5 w-5" /> 退出登录
              </Button>
            </form>
            <Button variant="outline" onClick={() => { startLoading(); router.push('/dashboard'); }} className="w-full">
              <LayoutDashboard className="mr-2 h-5 w-5" /> 前往仪表盘
            </Button>
          </CardFooter>
        </Card>
      </LoginPageContentWrapper>
    );
  }

  return (
    <LoginPageContentWrapper>
      <Script
        src="https://static.geetest.com/v4/gt4.js"
        strategy="afterInteractive"
        onLoad={() => {
          // console.log("Geetest SDK loaded."); // For debugging
          setSdkReady(true);
        }}
        onError={(e) => {
          console.error("Geetest SDK script loading error:", e);
        }}
      />
      <div className="w-full max-w-md">
        <Card className="shadow-xl">
          <Tabs value={authMode} onValueChange={(value) => handleAuthModeChange(value as 'login' | 'register')} className="w-full">
            <div className="flex justify-center pt-4 mb-4">
              <TabsList>
                <TabsTrigger value="login">登录</TabsTrigger>
                <TabsTrigger value="register">注册</TabsTrigger>
              </TabsList>
            </div>

            {/* Single Geetest container, outside TabsContent */}
            <div className="px-6 pb-4 space-y-2"> {/* Added padding for consistency with CardContent */}
              <Label htmlFor="captcha-container">人机验证</Label>
              <div id="captcha-container" className="w-full min-h-[44px] [&_.geetest_btn]:w-full [&_.geetest_btn_logo]:translate-y-[-2px]! [&_.geetest_btn_logo]:mr-1! [&_.geetest_btn_text]:translate-y-[-2px]! border border-input rounded-md flex items-center justify-center">
                {!sdkReady && <p className="text-sm text-muted-foreground p-3 text-center">正在加载人机验证...</p>}
                {sdkReady && !process.env.NEXT_PUBLIC_GEETEST_CAPTCHA_ID && <p className="text-sm text-destructive p-3 text-center">人机验证配置ID缺失。</p>}
              </div>
              {state.errors?.captcha && <p className="text-sm text-destructive">{state.errors.captcha[0]}</p>}
            </div>


            <TabsContent value="login">
              <CardHeader className="text-center pt-2 pb-2">
                <CardTitle className="text-2xl font-bold">登录 MineVent</CardTitle>
                <CardDescription>
                  输入您的 Minecraft 用户名和密码进行登录。
                </CardDescription>
              </CardHeader>
              <form action={formAction}>
                <CardContent className="space-y-4">
                  <input type="hidden" name="authMode" value="login" />
                  {captchaResult && ( // These are still needed for form submission
                    <>
                      <input type="hidden" name="lot_number" value={captchaResult.lot_number} />
                      <input type="hidden" name="captcha_output" value={captchaResult.captcha_output} />
                      <input type="hidden" name="pass_token" value={captchaResult.pass_token} />
                      <input type="hidden" name="gen_time" value={captchaResult.gen_time} />
                    </>
                  )}
                  <div className="space-y-2">
                    <Label htmlFor="loginMinecraftUsername">Minecraft 用户名</Label>
                    <Input
                      id="loginMinecraftUsername"
                      name="minecraftUsername"
                      type="text"
                      placeholder="例如：Steve"
                      required
                      aria-describedby="login-username-error"
                    />
                    {state.errors?.minecraftUsername && authMode === 'login' && <p id="login-username-error" className="text-sm text-destructive">{state.errors.minecraftUsername[0]}</p>}
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="loginPassword">密码</Label>
                    <Input
                      id="loginPassword"
                      name="password"
                      type="password"
                      placeholder="输入您的密码"
                      required
                      aria-describedby="login-password-error"
                    />
                    {state.errors?.password && authMode === 'login' && <p id="login-password-error" className="text-sm text-destructive">{state.errors.password[0]}</p>}
                  </div>
                  <p className="text-xs text-muted-foreground pt-1">
                      重要提示：此为原型系统，密码将以明文存储。请勿使用真实密码。
                  </p>
                  
                  {state?.message && authMode === 'login' && !state.success && (!state.errors || Object.keys(state.errors).length === 0 || state.errors.form) && (
                    <p className="text-sm text-destructive flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message || state.errors?.form?.[0]}</p>
                  )}

                  <Button type="submit" className="w-full" disabled={isPending || !isCaptchaVerified}>
                    {isPending ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : <UserPlus className="mr-2 h-5 w-5" />}
                    登录
                  </Button>
                </CardContent>
              </form>
            </TabsContent>
            <TabsContent value="register">
              <CardHeader className="text-center pt-2 pb-2">
                <CardTitle className="text-2xl font-bold">注册 MineVent 账户</CardTitle>
                <CardDescription>
                  使用您的 Minecraft 用户名创建新账户。
                </CardDescription>
              </CardHeader>
              <form action={formAction}>
                <CardContent className="space-y-4">
                  <input type="hidden" name="authMode" value="register" />
                   {captchaResult && (
                    <>
                      <input type="hidden" name="lot_number" value={captchaResult.lot_number} />
                      <input type="hidden" name="captcha_output" value={captchaResult.captcha_output} />
                      <input type="hidden" name="pass_token" value={captchaResult.pass_token} />
                      <input type="hidden" name="gen_time" value={captchaResult.gen_time} />
                    </>
                  )}
                  <div className="space-y-2">
                    <Label htmlFor="registerMinecraftUsername">Minecraft 用户名</Label>
                    <Input
                      id="registerMinecraftUsername"
                      name="minecraftUsername"
                      type="text"
                      placeholder="3-16位字母、数字或下划线"
                      required
                      aria-describedby="register-username-error"
                    />
                    {state.errors?.minecraftUsername && authMode === 'register' && <p id="register-username-error" className="text-sm text-destructive">{state.errors.minecraftUsername[0]}</p>}
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="registerPassword">密码</Label>
                    <Input
                      id="registerPassword"
                      name="password"
                      type="password"
                      placeholder="至少6位字符"
                      required
                      aria-describedby="register-password-error"
                    />
                    {state.errors?.password && authMode === 'register' && <p id="register-password-error" className="text-sm text-destructive">{state.errors.password[0]}</p>}
                  </div>
                   <div className="space-y-2">
                    <Label htmlFor="confirmPassword">确认密码</Label>
                    <Input
                      id="confirmPassword"
                      name="confirmPassword"
                      type="password"
                      placeholder="再次输入密码"
                      required
                      aria-describedby="confirm-password-error"
                    />
                    {state.errors?.confirmPassword && authMode === 'register' && <p id="confirm-password-error" className="text-sm text-destructive">{state.errors.confirmPassword[0]}</p>}
                  </div>
                  <p className="text-xs text-muted-foreground pt-1">
                      重要提示：此为原型系统，密码将以明文存储。请勿使用真实密码。
                  </p>
                  
                  {state?.message && authMode === 'register' && !state.success && (!state.errors || Object.keys(state.errors).length === 0 || state.errors.form) && (
                     <p className="text-sm text-destructive flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message || state.errors?.form?.[0]}</p>
                  )}

                  <Button type="submit" className="w-full" disabled={isPending || !isCaptchaVerified}>
                    {isPending ? <Loader2 className="mr-2 h-5 w-5 animate-spin" /> : <UserPlus className="mr-2 h-5 w-5" />}
                    创建账户
                  </Button>
                </CardContent>
              </form>
            </TabsContent>
          </Tabs>
          <CardFooter className="flex flex-col gap-4 pt-6">
            {isDebugMode && (
              <Button
                type="button"
                variant="secondary"
                className="w-full border-dashed border-primary/50 hover:border-primary"
                onClick={handleDebugLogin}
              >
                <Eye className="mr-2 h-5 w-5" />
                调试：以 dream_pep 身份查看仪表盘
              </Button>
            )}
            <p className="text-sm text-muted-foreground">
              遇到问题？请联系<LoadingLink href="#" className="font-medium text-primary hover:underline">管理员</LoadingLink>。
            </p>
          </CardFooter>
        </Card>
      </div>
    </LoginPageContentWrapper>
  );
}
    

    